#searchDiv{
    padding: 20px;
    width: 75%;
    margin: 10px 0;
    background-color: khaki;
}

#searchDiv input{
    width: 75%;
    margin: 0 5px;
    height: 30px;
}

#searchDiv :last-child{
    background: linear-gradient(to right, pink, green);
    width: 20%;
    height: 40px;
    font-weight: bold;
}